.contact-nav {
  display: block;
  padding-left: 6%;
  font-size: 15px;
  margin-top: 10px;
  color: grey;
}

.nav {
  display: flex;
  align-items: center;
}

.navbar {
  display: flex;
  justify-content: space-around;
  padding: 16px;
  align-items: center;
}

.nav-logo {
  display: flex;
  align-items: center;
  gap: 10px;
}

.nav-logo p {
  color: #171717;
  font-size: 38px;
  font-weight: 600;
}

.nav-logo img {
  width: 40px;
  height: 50px;
}

.nav-menu {
  display: flex;
  align-items: center;
  list-style: none;
  gap: 50px;
  color: #626262;
  font-size: 20px;
  font-weight: 500;
  justify-content: flex-start;
}

.nav-menu li {
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* Thêm đoạn này để căn trái */
  justify-content: center;
  gap: 3px;
  cursor: pointer;
  text-align: left; /* Thêm đoạn này để căn trái */
}

.nav-menu hr {
  border: none;
  width: 80%;
  height: 3px;
  border-radius: 10px;
  background-color: #41a0ff;
}

.nav-menu-active {
  color: #ff4141;
}

.nav-login-cart {
  display: flex;
  align-items: center;
  gap: 45px;
}

.nav-login-cart button {
  width: 157px;
  height: 58px;
  outline: none;
  border: 1px solid #7a7a7a;
  border-radius: 75px;
  color: #515151;
  font-weight: 500;
  background: white;
  cursor: pointer;
  font-size: 17px;
}

.nav-login-cart button:active {
  background: #f3f3f3;
}

.nav-cart-count {
  width: 22px;
  height: 22px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -35px;
  margin-left: -55px;
  border-radius: 11px;
  font-size: 14px;
  background-color: #41a0ff;
  color: white;
}

.nav-dropdown {
  display: none;
}

@media (max-width: 1280px) {
  .navbar {
    padding: 12px 50px;
  }
  .nav-logo img {
    width: 40px;
  }
  .nav-logo p {
    font-size: 25px;
  }
  .nav-menu {
    gap: 30px;
    font-size: 16px;
  }
  .nav-login-cart {
    gap: 30px;
  }
  .nav-login-cart button {
    width: 120px;
    height: 45px;
    font-size: 16px;
  }
  .nav-cart-count {
    margin-left: -40px;
    font-size: 12px;
  }
}

@media (max-width: 1024px) {
  .navbar {
    padding: 12px 30px;
  }
  .nav-menu {
    gap: 25px;
    font-size: 14px;
  }
  .nav-login-cart button {
    width: 80px;
    height: 35px;
    font-size: 14px;
  }
  .nav-login-cart img {
    width: 30px;
  }
  .nav-cart-count {
    width: 18px;
    height: 18px;
  }
}
@media (max-width: 800px) {
  .navbar {
    padding: 10px 0px;
  }
  .nav-dropdown {
    margin-left: 10px;
    display: block;
    rotate: -90deg;
    transition: 0.5s;
  }
  .nav-menu {
    display: none;
    height: 80px;
    width: -webkit-fill-available;
    position: absolute;
    background-color: white;
    justify-content: center;
    top: 99px;
  }
  .nav-menu-visible {
    display: flex;
  }
  .nav-dropdown.open {
    transform: rotate(90deg);
  }
}

@media (max-width: 500px) {
  .navbar {
    width: 8px 0px;
    gap: 0;
  }
  .nav-menu {
    display: none;
    height: 80px;
    width: -webkit-fill-available;
    position: absolute;
    background-color: white;
    justify-content: center;
    top: 99px;
  }
  .nav-menu-visible {
    display: flex;
  }
  .nav-dropdown.open {
    transform: rotate(90deg);
  }
  .nav-logo {
    transform: scale(0.8);
  }
  .nav-menu {
    height: 70px;
    top: 97px;
  }
  .nav-login-cart {
    transform: scale(0.8);
  }
}
