.new-collections {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  padding-bottom: 50px;
  margin-bottom: 100px;
}

.new-collections h1 {
  color: #171717;
  font-size: 50px;
  font-weight: 600;
}

.new-collections hr {
  width: 200px;
  height: 6px;
  border-radius: 10px;
  background: #252525;
}

.collections {
  grid-template-columns: 1fr 1fr 1fr 1fr;
  display: grid;
  margin-top: 50px;
  gap: 30px;
}

.collections img {
  width: 350px;
  height: 418px;
}

@media (max-width: 1280px) {
  .new-collections h1 {
    font-size: 40px;
  }
  .new-collections hr {
    width: 160px;
    height: 4px;
  }
  .collections {
    gap: 20px;
    margin-top: 30px;
  }

  .collections img {
    width: 192px;
    height: 192px;
  }
}

@media (max-width: 1024px) {
  .new-collections h1 {
    font-size: 30px;
  }
  .new-collections hr {
    width: 120px;
    height: 3px;
  }
  .collections {
    gap: 15px;
    margin-top: 20px;
  }
  .collections img {
    width: 192px;
    height: 192px;
  }
}

@media (max-width: 800px) {
  .new-collections h1 {
    font-size: 20px;
  }
  .new-collections hr {
    width: 120px;
  }
  .collections {
    gap: 5px;
  }
  .collections img {
    width: 130px;
    height: 120px;
  }
}

@media (max-width: 500px) {
  .collections {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }
  .collections img {
    width: 130px;
    height: 120px;
  }
}
