.loginSignup {
  padding-top: 10px;
}

.loginSignup-container {
  width: 580px;
  height: 600px;
  background: white;
  margin: auto;
  padding: 40px 60px;
}

.loginSignup-container h1 {
  margin: 20px 0;
  text-align: center;
}

.loginSignup-fields {
  display: flex;
  flex-direction: column;
  gap: 29px;
  margin-top: 30px;
}
.loginSignup-login span {
  cursor: pointer;
}

.loginSignup-fields input {
  height: 50px;
  width: 100%;
  padding-left: 20px;
  border: 1px solid #c9c9c9;
  outline: none;
  color: #5c5c5c;
  font-size: 18px;
  border-radius: 15px;
}

.loginSignup-container button {
  width: 580px;
  height: 72px;
  color: white;
  background: #41a0ff;
  margin-top: 30px;
  border: none;
  font-size: 24px;
  font-weight: 500;
  cursor: pointer;
}

.loginSignup-login {
  margin-top: 20px;
  color: #5c5c5c;
  font-size: 18px;
  font-weight: 500;
}

.loginSignup-login span {
  color: #41a0ff;
  font-weight: 600;
}

.loginSignup-agree {
  display: flex;
  align-items: center;
  margin-top: 25px;
  gap: 20px;
  color: #5c5c5c;
  font-size: 18px;
  font-weight: 500;
}

@media (max-width: 1280px) {
  .loginSignup {
    padding: 50px;
  }
  .loginSignup-container {
    width: auto;
    max-width: 500px;
    max-height: 500px;
  }
  .loginSignup-container h1 {
    margin: 10px 0px;
  }
  .loginSignup-fields {
    gap: 20px;
    margin-top: 20px;
  }
  .loginSignup-fields input {
    height: 40px;
    width: 93%;
  }
  .loginSignup-container button {
    width: 100%;
  }
  .loginSignup-login {
    font-size: 16px;
  }
  .loginSignup-agree {
    font-size: 14px;
  }
}
@media (max-width: 800px) {
  .loginSignup-container {
    padding: 20px 30px;
  }
  .loginSignup-container h1 {
    font-size: 24px;
  }
  .loginSignup-fields {
    margin-top: 20px;
  }
  .loginSignup-fields input {
    height: 40px;
  }
  .loginSignup-container button {
    height: 50px;
    font-size: 16px;
  }
}
